/* Google Font Imports */
@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500,500i,700,700i");
@import url("https://fonts.googleapis.com/css?family=Rubik:500");
/* Font Variable Declarations */
/* Color Palette For App */
/* Whites */
/* Grays */
/* Blues */
/* Oranges */
/* Box Shadows */
html, body {
  height: 100%;
}
body {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  margin: 0 auto;
  background: #FFFFFF;
  height: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.g__body__gray {
  background: rgba(38, 70, 83, 0.1);
}

/* global container */
.g__container {
  position: relative;
  max-width: 1024px;
  margin: 0 auto;
}

/* Disable Scrolling for full screen pop ups */
.no-overflow {
  overflow: hidden;
}

/* Toggle Styles for Filters */
.filter {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.25s ease-out;
}
.filter--show {
    visibility: visible;
    opacity: 1;
}

/* Filtering Required Message*/
.filter-required {
  padding: 2rem;
  letter-spacing: 0.5px;
  color: #959595;
}

/* Various Settings */
/* Animations */
@-webkit-keyframes dotslide {
0% {
    left: -10%;
}
100% {
    left: 120%;
}
}
@keyframes dotslide {
0% {
    left: -10%;
}
100% {
    left: 120%;
}
}

/* Resets */
h1, h2, h3, h4, h5, p {
  margin: 0;
  padding: 0;
  font-size: 1rem;
}
input {
  outline: none;
  border: none;
}
ul {
  padding: 0;
}
button {
  outline: none;
  border: none;
  background: transparent;
}

/* Styles for the Filter pages (state, city, company, industry) */
.filters {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: +1;
  bottom: 0;
  left: 0;
  background: #F6F6F6;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
.filters--margin {
    margin-right: 1rem;
    margin-left: 1rem;
}
.filters__container {
    position: relative;
    min-height: 100%;
    max-width: 768px;
    margin: 0 auto;
}
.filters__section {
    min-height: 200px;
    position: relative;
    margin: -2.5rem auto 0 auto;
    max-width: 768px;
}
.filters__section__input {
      width: 100%;
      margin: 0 5rem 0 2rem;
      font-family: "Roboto", sans-serif;
      font-size: 1rem;
      background: transparent;
      color: #959595;
      padding: 1rem;
}
.filters__section__input::-webkit-input-placeholder {
        color: #B8B8B8;
        font-family: "Roboto", sans-serif;
        font-style: italic;
        font-weight: 400;
        letter-spacing: 1px;
        font-size: 1rem;
}
.filters__section__input:-ms-input-placeholder {
        color: #B8B8B8;
        font-family: "Roboto", sans-serif;
        font-style: italic;
        font-weight: 400;
        letter-spacing: 1px;
        font-size: 1rem;
}
.filters__section__input::placeholder {
        color: #B8B8B8;
        font-family: "Roboto", sans-serif;
        font-style: italic;
        font-weight: 400;
        letter-spacing: 1px;
        font-size: 1rem;
}
.filters__section__input-wrapper {
      width: calc(100% - 4rem);
      position: relative;
      background: #FFFFFF;
      border-radius: 100px;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
      margin: 0 auto 1rem auto;
      padding: 1rem 0;
      position: relative;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.filters__section__list {
      list-style: none;
      overflow-y: auto;
      max-height: 100vh;
      margin-top: 2rem;
}
.filters__section__list::-webkit-scrollbar {
        display: none;
}
.filters__section__list__item {
        margin: 0.25rem 0;
        padding: 1rem;
        color: #363636;
        letter-spacing: 1px;
        font-weight: 300;
        transition: background 0.2s ease-in;
        position: relative;
        border-radius: 5px;
}
.filters__section__list__item--selected:after {
          content: '';
          position: absolute;
          right: 1rem;
          height: 20px;
          width: 20px;
          top: 50%;
          -webkit-transform: translateY(-50%);
                  transform: translateY(-50%);
}
.filters__section__list__item:hover {
          cursor: pointer;
          background: rgba(38, 70, 83, 0.1);
}



